:root {
  --green-color:#557b3e;
  --white-color:#ffffff;
}
.App {
  text-align: center;
  font-family: "LDF Comic Sans MS", "Comic Sans MS", cursive;     /*** Letra ***/
}
@font-face {
  font-family: "LDF Comic Sans MS";
  font-style: normal;
  font-weight: normal;
  src: url("./assets/fonts/LDF-ComicSans/LDFComicSans.ttf") format("truetype");
}
.App-link {
  color: var(--white-color);
}

/****************************** HEADER ******************************/
.App-header {
  position: absolute;
  display: flex;
  flex-direction: column;
  height: 5vmin;
  width: 50%;
  background-color: rgba(0, 0, 0, 0.3);
  font-size: 2vmin;
  color: var(--white-color);
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.7);
  align-items: center;
  justify-content: center;
  vertical-align: middle;
  user-select: none;                    /* Impide seleccionar la imagen con el mouse*/
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  z-index: 1;
}
.App-slogan {
  position: absolute;
  pointer-events: none;
  left: 2vw;
}
.App-logo {
  height: 3vmin;
}
.App-titulo{
}
.tiempo {
  position: absolute;
}
.marcador {
  position: absolute;
  right: 2vw;
}
.botonStart {
  /* width: 5vmin; */
}

/******************************* BODY *******************************/
.App-body {
  /* position: absolute; */
  background-color: var(--green-color);
  height: 100vmin;
  top:0vmin;
  /* left: 50vw; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 3vmin;
  color: white;
  user-select: none;                    /* Impide seleccionar la imagen con el mouse*/
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

.cancha {
  position: absolute;
  display: flex;
  height: 95vmin;
  width: 90%;
  border: 4px solid var(--white-color);
  align-items: center;
  justify-content: center;
  vertical-align: middle;
  /* background: url("/public/cancha.jpg") no-repeat;
  background-size:100%; */
}
.canchaMitad {
  border-left: 4px solid var(--white-color);
  height: 100%;
  margin-top: 1.5vmin;
  margin-left: 50%;
  margin-right: 50%;
}
.canchaCirculoCentral {
  position: absolute;
  border: 4px solid var(--white-color);
  border-radius: 100vmax;
  height: 30vmin;
  width: 30vmin;
  margin-left: auto;
  margin-right: auto;
}
.canchaAreaGrande {
  position: absolute;
  border: 4px solid var(--white-color);
  height: 55vmin;
  width: 24vmin;
}
#canchaAreaGrande1 { left: 0; border-left: none; }
#canchaAreaGrande2 { right: 0; border-right: none; }
.canchaAreaChica {
  position: absolute;
  border: 4px solid var(--white-color);
  height: 25vmin;
  width: 9vmin;
}
#canchaAreaChica1 { left: 0; border-left: none; }
#canchaAreaChica2 { right: 0; border-right: none; }
.canchaMediaLuna {
  position: absolute;
  display: flex;
  height: 25vmin;
  width: 25vmin;
}
#canchaMediaLuna1 { left: 9vmin; }
#canchaMediaLuna2 { right: 9vmin; }
.canchaMediaLunaCirculo {
  position: absolute;
  border: 4px solid var(--white-color);
  border-radius: 100%;
  height: 25vmin;
  width: 25vmin;
}
#canchaMediaLunaCirculo1 { left: -2vmin; }
#canchaMediaLunaCirculo2 { right: -2vmin; }

.canchaMediaLunaOffset {
  position: absolute;
  background-color: var(--green-color);
  height: 30vmin;
  width: 20vmin;
}
#canchaMediaLunaOffset1 { left: -20%; }
#canchaMediaLunaOffset2 { left: 40%; }

.canchaPorteria {
  position: absolute;
  border: 4px solid var(--white-color);
  height: 15vmin;
  width: 5vmin;
}
#canchaPorteria1 { left: -6vmin; border-right:none;}
#canchaPorteria2 { right: -6vmin; border-left:none; }

.balon {
  height: 7vmin;
  pointer-events: none;
  position: absolute;
}

.jugador {
  height: 12vmin;
  pointer-events: none;
  position: absolute;
}
